import Vue from 'vue'
import Router from 'vue-router'

import user from './routes/user'
import auth from './routes/auth'
import home from './routes/home'


Vue.use(Router)

const router = new Router({
  routes: [
    ...user,
    ...auth,
    ...home,
  ],
  mode: 'history'
});

export default router



