
var ApiUrl = "user";


// Initial State
const state = {
  users: {},
  user: [],
  isLoading: true,
};

// Getters
const getters = {
  users(state) {
    return state.users;
  },
  user(state) {
    return state.user
  },
  userIsLoading(state) {
    return state.isLoading
  },
};

// Actions
const actions = {

  async getAllUsers(context){
    context.commit("startLoading");
    return apiService.get(ApiUrl)
      .then(({ data }) => {
        context.commit("setUsers", data.data);
        context.commit("endLoading");
      })
      .catch(error => {
        throw new Error(error)
      })
  },
  async getUser(context, userSlug){
    context.commit("startLoading");
    return apiService.get(ApiUrl, userSlug)
      .then(({ data }) => {
        context.commit("setUser", data.data);
        context.commit("endLoading");
      })
      .catch(error => {
        throw new Error(error)
      })
  },

  async createUser(context, payload) {
    const { data } = await apiService.post(ApiUrl, payload);
    context.commit("setUser", data);
    return data;
  },

  async updateUser(context, payload) {
    console.log(payload);
    const { data } = await apiService.update(ApiUrl, payload.id, payload);
    context.commit("setUser", data);
    return data;
  },

};

// Mutations
export const mutations = {
  startLoading(state) {
    state.isLoading = true;
  },
  endLoading(state) {
    state.isLoading = false;
  },
  setUsers(state, users){
    state.users = users;
  },
  setUser(state, user){
    state.user = user
  }
};


export default {
  state,
  getters,
  actions,
  mutations
}



