import Index from '../../components/home/index'
import Contact from '../../components/home/contact'
import Message from "../../components/home/Message";

export default [
  {
    path: '/',
    component: Index,
    children: [
      {
        name: 'message',
        path: 'message',
        component: Message,
      },
      {
        name: 'contact',
        path: 'contact',
        component: Contact,
      },
    ]
  }
]



