// action names
const FETCH_START = "setAuthLoading";
const FETCH_END = "resetAuthLoading";

// mutation names
const SET_AUTH_USER = "setAuthUser";
const SET_AUTH_ERROR = "setAuthError";
const AUTH_PURGE = "AuthPurge";



//Initial State
const state = {
  errors: null,
  user: [],
  isAuthenticated: false,
  isLoading: true,
};



// Getters
const getters = {
  authUser(state) {
    return state.user
  },
  isAuthenticated(state){
    return state.isAuthenticated;
  },
  authIsLoading(state){
    return state.isLoading;
  }
};

// Actions
const actions = {
  login(context, credentials){
    return new Promise(resolve => {
      context.commit(FETCH_START);
      //api call
    });
  },
  logout(context){
    context.commit(AUTH_PURGE)
  },
  checkAuth(context){
    //check token state
    if(true){
      //set token;
    }else {
      context.commit(AUTH_PURGE)
    }
  },
};

// Mutations
const mutations = {
  [FETCH_START](state) {
    state.isLoading = true;
  },
  [FETCH_END](state) {
    state.isLoading = false;
  },
  [SET_AUTH_ERROR](state, errors){
    state.errors = errors
  },
  [SET_AUTH_USER](state, payload) {
    console.log(payload.user);
    state.isAuthenticated = true;
    state.user = payload.user;
    state.errors = {};
    //set token
  },
  [AUTH_PURGE](state){
    state.isAuthenticated = false;
    state.user = [];
    state.errors = {};
    //destroy token
  }
};

export default {
  state,
  getters,
  actions,
  mutations
}



